import { Component, OnInit, ViewChild, Inject } from '@angular/core';
import { AgentService } from '../../shared/services/agent';
import { MatTableDataSource } from '@angular/material';
import { MatPaginator } from '@angular/material/paginator';
import { SelectionModel } from '@angular/cdk/collections';
import { MatSort } from '@angular/material/sort';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'view-add-customer',
  templateUrl: './view-customer.component.html',
  styleUrls: ['./view-customer.component.css']
})
export class ViewCustomerComponent implements OnInit {
  selection = new SelectionModel(true, []);
  dataSource: MatTableDataSource<any>;
  MyCustomer;

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  displayedColumns = ['S No', 'Name', 'Mobile', 'Email', 'DOB', 'Address', 'createdAt', 'editCustomer']; //, 'Start Time', 'End Time'

  constructor(private service: AgentService, public dialog: MatDialog, private toastrService: ToastrService) { }

  ngOnInit() {
    this.service.getAllAgentCustomer().subscribe((data) => {
      console.log(data);
      this.MyCustomer = data['message'];
      this.dataSource = new MatTableDataSource(this.MyCustomer);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    })
  }

  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }

  //edit customer
  editCustomer(id) {
    //debugger;
    this.service.getAgentSingleCustomer(id).subscribe((data) => {
      //debugger;
      console.log(data);
      if (data['success'] == true) {
        console.log(data);
        const dialogRef = this.dialog.open(EditCustomerDetailsComponent, {
          width: '50%',
          data: data['message'],
          // disableClose: true
        });

        dialogRef.afterClosed().subscribe(result => {
          //debugger;
          console.log(result);
          if (result != undefined && result !=="" && result !==null) {
            let id = result['id'];
            delete result['id'];
            console.log(result);

            this.service.updateAgentCustomer(id, result).subscribe((data)=>{
              //debugger;
              console.log(data);
              if(data['success']== true){
                this.showToaster('customer data updated successfully');
                this.ngOnInit();
              }
              else{
                this.ErrorSuccess();
              }
            })
          } else {
            console.log('No thanks button clicked');
          }

        });
      }
    })
  }

  //dialog box
  openCustomerDetails(id): void {
    //debugger;
    this.service.getAgentSingleCustomer(id).subscribe((data) => {
      //debugger;
      console.log(data);
      if (data['success'] == true) {
        console.log(data);
        const dialogRef = this.dialog.open(ShowCustomerDetailsComponent, {
          width: '50%',
          data: data['message'],
          // disableClose: true
        });

        dialogRef.afterClosed().subscribe(result => {

          if (result != undefined) {

          } else {
            console.log('No thanks button clicked');
          }

        });
      }
    })
  }


    //notification
    showToaster(msg) {
      this.toastrService.success(msg);
    }
    showLoginSuccess(){
      this.toastrService.success('User data saved successfully');
    }
    ErrorSuccess() {
      this.toastrService.error('Failed: Sth Error has happened');
    }
    infoSuccess() {
      this.toastrService.info('info msg', 'Info!');
    }
    warningSuccess() {
      this.toastrService.warning('this is warning');
    }

}

//Dialog box for customer details
//Dialog Box customer detials
@Component({
  selector: 'showCustomerDetails-dialog',
  templateUrl: 'showCustomerDetails.html',
  //styleUrls: ['./customerDetails.css'],
})
export class ShowCustomerDetailsComponent implements OnInit {
  constructor(
    public dialogRef: MatDialogRef<ShowCustomerDetailsComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any, private formBuilder: FormBuilder) {
    console.log('from ts:', data);
  }

  ngOnInit() {
    this.data['lastCustomerStatus'] = this.data['lastCustomerStatus'] == 0 ? 'Dealdone': (this.data['lastCustomerStatus'] == 1 ? 'Introductioncalls': (this.data['lastCustomerStatus'] == 2 ? 'Followupcalls':(this.data['lastCustomerStatus'] == 3 ? 'Callback':
       (this.data['lastCustomerStatus'] == 4 ? 'Voicemail':(this.data['lastCustomerStatus'] == 5 ? 'Blacklist':(this.data['lastCustomerStatus'] == 6 ? 'Numberdoesnotmatch':(this.data['lastCustomerStatus'] == 7 ? 'Numberdoesnotmatch':' Duplicate')))))));

    this.data['registrationStatus'] = this.data['registrationStatus'] == 0 ? 'low':(this.data['registrationStatus'] == 1 ? 'medium ':'high')
    this.data['successRatePotential'] = this.data['successRatePotential'] == 0 ? 'lead':(this.data['successRatePotential'] == 1 ? 'demo ':'real')
  }

  onNoClick(): void {
    this.dialogRef.close();
  }
}

//Dialog Box customer detials
@Component({
  selector: 'editCustomerDetails-dialog',
  templateUrl: 'editCustomerDetails.html',
  styleUrls: ['./editCustomerDetails.css'],
})
export class EditCustomerDetailsComponent implements OnInit {
  formGroup: FormGroup;

  constructor(
    public dialogRef: MatDialogRef<EditCustomerDetailsComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any, private formBuilder: FormBuilder) {
    console.log('from ts:', data);
  }

  ngOnInit() {
    this.createForm();

    this.setValue();
  }

  createForm() {
    this.formGroup = this.formBuilder.group({
      'Name': [null, [Validators.required]], //, Validators.pattern(mobileregex)
      'Mobile': [{value:null,disabled: true}, [Validators.required]],
      'DOB': [null, [Validators.required]],
      'Address': [null, [Validators.required]],
      'Email': [null, [Validators.required]],
      'lastContacted': [null, [Validators.required]],
      'lastCustomerStatus': [null, [Validators.required]],
      'registrationStatus': [null, [Validators.required]],
      'successRatePotential': [null, [Validators.required]],
      'Note': [null, [Validators.required]],
      'id':[null, [Validators.required]]
    });
  }

  setValue() {
    this.formGroup.get('Name').setValue(this.data['Name']);
    this.formGroup.get('Mobile').setValue(this.data['Mobile']);
    this.formGroup.get('DOB').setValue(new Date(this.data['DOB']));
    this.formGroup.get('Address').setValue(this.data['Address']);
    this.formGroup.get('Email').setValue(this.data['Email']);
    this.formGroup.get('lastContacted').setValue(this.data['lastContacted']);
    this.formGroup.get('lastCustomerStatus').setValue(this.data['lastCustomerStatus']);
    this.formGroup.get('registrationStatus').setValue(this.data['registrationStatus']);
    this.formGroup.get('successRatePotential').setValue(this.data['successRatePotential']);
    this.formGroup.get('Note').setValue(this.data['Note']);
    this.formGroup.get('id').setValue(this.data['_id']);
  }

  onNoClick(): void {
    this.dialogRef.close();
  }

  onSubmit(data){
    //debugger;
    if(!this.formGroup.valid){
      return 
    }
    console.log(data);
    this.dialogRef.close(data);
  }


  Agent = [
    { value: 0, name: "Deal done" },
    { value: 1, name: "Introduction calls" },
    { value: 2, name: "Follow up calls" },
    //{value:3, name: "Deal done"},
    { value: 3, name: "Callback" },
    { value: 4, name: "Voicemail" },
    { value: 5, name: "Blacklist" },
    { value: 6, name: "Number does not match" },
    { value: 7, name: "Underage" },
    { value: 8, name: "Duplicate" },
  ]

  SuccessRate = [
    { value: 0, name: "low" },
    { value: 1, name: "medium" },
    { value: 2, name: "high" } //low medium high
  ]

  RegistrationStatus = [
    { value: 0, name: "lead" },
    { value: 1, name: "demo" },
    { value: 2, name: "real" }
  ]

}
